//存储el-form表单校验规则

export default {
  validateAccount (rule, value, callback){
    if(!/^[A-Za-z0-9]+$/.test(value)){
      callback(new Error('请输入由字母和数字组成的用户名'))
    }
    else if (!/^[A-Za-z0-9]{0,13}$/.test(value)) {
      callback(new Error('请输入13个字符以内的用户名'))
    } else {
      callback()
    }
  },
  validatePassword (rule, value, callback) {
    if(!/^[A-Za-z0-9]+$/.test(value)){
      callback(new Error('请输入由字母和数字组成的密码'))
    }
    else if (!/^[A-Za-z0-9]{0,13}$/.test(value)) {
      callback(new Error('请输入13个字符以内的密码'))
    } else {
      callback()
    }
  }
}
